html {
  box-sizing: border-box; }

html, body {
  overflow-x: hidden;
  height: 100vh;
  width: 100vw; }

nav li {
  text-decoration: none;
  list-style: none; }

#nav-site {
  display: flex;
  align-items: center; }
  #nav-site ul {
    display: flex;
    align-self: center;
    width: 100%;
    height: 15px; }
    #nav-site ul a {
      text-align: center;
      width: 25%;
      border-right: 2px #C9D990 solid; }
    #nav-site ul a:last-child {
      border-right: none; }
    #nav-site ul a:hover {
      background-color: #C9D990; }

#nav-ecommerce ul {
  display: flex; }
  #nav-ecommerce ul a {
    text-align: center;
    flex-basis: 25%;
    background-color: #94B82F;
    padding: 25px 0;
    border-right: 0.1px #C9D990 solid;
    min-width: 150px; }
    #nav-ecommerce ul a:hover {
      background-color: #C9D990; }

@media screen and (max-width: 650px) {
  #nav-ecommerce ul {
    flex-direction: column; }
    #nav-ecommerce ul a {
      font-size: 15px;
      padding: 10px;
      border-top: 1px solid white; } }

header {
  display: grid;
  grid-template-rows: 40px 40px 40px auto;
  grid-template-columns: 15% 40% auto;
  font-family: "Comfortaa"; }
  header #nav-site {
    grid-column: 3 / 4;
    grid-row: 1 / 1;
    font-size: calc(0.65em + 0.2vw); }
  header .logo {
    justify-self: left;
    height: inherit;
    grid-column-start: 1;
    grid-row: 1 / 4; }
    header .logo svg {
      height: 90%;
      width: 90%;
      min-width: 50px; }
  header .search-box {
    display: flex;
    min-width: 150px;
    grid-column: 2 / 3;
    grid-row: 2 / 2;
    background-color: white;
    border: 2px solid #94B82F; }
    header .search-box input {
      border: #C9D990;
      margin: auto;
      width: 80%; }
  header #user-area {
    grid-row-start: 2;
    background-color: #C9D990;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom-left-radius: 60px;
    margin-left: 10px; }
  header #claim {
    grid-row: 3 / 3;
    margin: auto 0; }

@media screen and (max-width: 650px) {
  .wrapper-home {
    width: 90% !important;
    margin: auto; }
  header {
    font-size: 0.8em;
    grid-template-columns: auto auto auto; }
  .search-box {
    grid-column: 1 / 1; }
  #nav-site {
    grid-column: 2 / 4 !important;
    height: 30px;
    margin-right: 20px; }
    #nav-site ul li {
      margin: auto; }
  #user-area {
    min-width: 90px; }
    #user-area span {
      display: none; }
  #claim {
    display: none; } }

.brand-message {
  margin: 20px 0;
  background-color: #F3F3F3;
  padding: 5px;
  border-radius: 10px; }
  .brand-message ul {
    margin: auto;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .brand-message ul li {
      display: flex;
      width: 200px;
      align-items: center;
      margin: auto; }
      .brand-message ul li .fas {
        margin-right: 15px;
        font-size: calc(20px + 0.5vw); }

.wrapper-destacados {
  display: flex;
  gap: 20px; }

.article-mid {
  box-sizing: border-box;
  display: flex;
  flex-flow: column;
  flex-basis: 290px;
  background-color: rgba(255, 255, 255, 0);
  gap: 10px;
  align-items: center;
  padding: 30px;
  -webkit-box-shadow: 0px 6px 14px -1px rgba(222, 222, 222, 0.57);
  box-shadow: 0px 6px 14px -1px rgba(222, 222, 222, 0.57);
  border-radius: 20px;
  position: relative;
  padding-bottom: 60px; }
  .article-mid:hover {
    border: 2px solid #94B82F;
    padding: 28px;
    padding-bottom: 58px; }
  .article-mid h3 {
    text-align: center;
    margin: 0px; }
  .article-mid span {
    text-align: center; }
  .article-mid .primary-button {
    position: absolute;
    width: 70%;
    bottom: 20px; }
  .article-mid .amount {
    display: flex;
    justify-content: center;
    gap: 5px; }
    .article-mid .amount button {
      border: none; }
    .article-mid .amount input {
      width: 20%;
      text-align: center;
      border-radius: 5px;
      border: 1px #333333 solid; }
  .article-mid div span {
    font-size: 1.3em;
    font-weight: bolder;
    color: #532fb8; }
  @media screen and (max-width: 650px) {
    .article-mid .wrapper-destacados {
      margin-left: none !important; } }

.wrapper-destacados {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

footer .footer-nav {
  margin-top: 30px;
  display: flex;
  justify-content: space-evenly;
  background-color: #94B82F;
  padding: 20px 0; }
  footer .footer-nav div ul {
    margin-bottom: 10px; }

footer .regular-list * {
  color: white;
  text-decoration: none; }

footer .regular-list > li > ul {
  margin: 5px 0 0 15px;
  font-size: 0.7em;
  border-left: 1px solid white;
  padding: 0 10px; }

footer aside {
  display: flex;
  justify-content: space-between;
  padding: 0.5vw; }
  footer aside .logo {
    width: calc(90px + 2vw); }

footer .conditions {
  font-size: 0.8em; }

.list-horizontal {
  display: flex;
  flex-basis: 600px;
  justify-content: space-evenly;
  align-self: center; }

.business-info {
  display: flex;
  padding: 10px;
  border-top: 2px solid #F3F3F3;
  font-size: 0.8em;
  color: #333333;
  justify-content: center;
  gap: 1vw; }

@media screen and (max-width: 650px) {
  footer * {
    font-size: 0.8rem; }
  .footer-nav {
    flex-flow: column wrap;
    align-content: space-around;
    height: 100px; }
  .conditions {
    flex-wrap: column wrap;
    align-content: flex-end;
    text-align: end;
    gap: 5px; }
  .regular-list li ul {
    display: none; } }

hr {
  border: 0.5px solid #F3F3F3;
  transform: translate(-1000px);
  width: 5000px;
  left: 100px; }

.icon-mediumlight {
  border: none;
  background-color: white; }
  .icon-mediumlight i {
    color: #94B82F; }

.icon {
  color: grey;
  font-size: 1.2em;
  margin: 0 4px; }

.dropdown-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin-right: 5px;
  border: 1.5px solid #F3F3F3;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  padding: 0px;
  position: relative; }
  .dropdown-button i::after {
    display: flex;
    align-items: center;
    content: "";
    font-size: 0.8em;
    font-weight: bold;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f107";
    font-style: normal; }
  .dropdown-button .collapse::after {
    display: flex;
    align-items: center;
    content: "";
    font-size: 0.8em;
    font-weight: bold;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f106";
    font-style: normal; }
  .dropdown-button:hover {
    border-color: #333333; }

.dropdown-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px; }
  .dropdown-item > ul {
    display: none; }
  .dropdown-item > .visible {
    display: flex;
    margin-top: 5px;
    gap: 5px;
    flex-direction: column;
    margin-left: 40px;
    font-size: 0.8em; }
  .dropdown-item .dropdownChildCollapse {
    color: #333333;
    font-family: Roboto; }
  .dropdown-item div {
    display: flex; }

.primary-button {
  border: 0px #94B82F solid;
  background-color: #94B82F;
  padding: 5px;
  border-radius: 5px;
  width: 100%;
  font-weight: bold;
  color: white; }
  .primary-button:hover {
    background-color: #532fb8; }

.secondary-button {
  border: 2px #94B82F solid;
  background-color: white; }
  .secondary-button:hover {
    background-color: #C9D990; }

.context-button {
  border: 1px #333333 solid;
  color: #333333;
  background-color: white;
  font-weight: bolder; }
  .context-button:hover {
    background-color: #F3F3F3; }

.home h1 ~ * {
  margin-left: 30px; }
  @media screen and (max-width: 650px) {
    .home h1 ~ * {
      margin-left: 15px; } }

.home .wrapper-home {
  width: 50%;
  margin: auto; }

.wrap-list {
  display: flex;
  justify-content: space-evenly;
  flex-flow: column wrap; }

@media screen and (max-width: 650px) {
  .wrap-list {
    height: auto;
    gap: 5px; } }
